/* Header.css */
.container {
    display: flex;
    justify-content: flex-end;
}

.button {
    margin-left: 10px;
}

.button a {
    display: inline-block;
    padding: 10px 20px;
    border-radius: 12px;
    text-align: center;
    font-size: 16px;
    text-decoration: none;
    color: white;
    background-color: #007bff; 
}

.button a:hover {
    background-color: #0056b3; 
}
